import AnimationView from '../view/AnimationView';
import AnimationModel from '../model/AnimationModel';

export default class AnimationFrameController {
  constructor() {
    this.AnimationView = new AnimationView();
    this.fps = 1;
    this.slider = document.querySelector('#slider input');
    this.out = document.getElementById('out');
    this.frameCount = 0;
    this.startTime = 0;
    this.flag = true;
  }

  addEventListeners() {
    this.slider.addEventListener('input', () => { this.rewriteValue(); });
    document.getElementById('fullScreen').addEventListener('click', () => AnimationModel.fullScreenApi(document.getElementById('animation')));
    this.animate();
  }

  rewriteValue() {
    this.out.innerHTML = `${this.slider.value}FPS`;
    this.fps = this.slider.value;
  }

  animate() {
    this.startTime = new Date().getTime();
    return window.requestAnimationFrame(this.loop.bind(this));
  }

  loop() {
    const frames = document.querySelectorAll('#frame');
    const current = new Date().getTime();
    const time = current - this.startTime;
    if (time >= (1000 / this.fps)) {
      this.startTime = new Date().getTime();
      this.flag = true;
      window.requestAnimationFrame(this.loop.bind(this));
    }
    if (frames.length <= this.frameCount) {
      this.frameCount = 0;
    }
    if (this.flag) {
      this.AnimationView.drawAnimation(frames[this.frameCount]);
      this.frameCount += 1;
      this.flag = false;
    }
    window.requestAnimationFrame(this.loop.bind(this));
  }
}
