import CanvasView from '../view/CanvasView';

export default class Canvas {
  constructor(mod, canv) {
    this.canvas = canv;
    this.model = mod;
    this.img = new Image();
    this.drawingState = false;
    this.Canvasview = new CanvasView(canv);
    this.startCoord = {};
    this.shift = false;
    this.ctrl = false;
    this.curr = {};
    this.numCoord = document.getElementById('numCoord');
    this.left = false;
  }

  startDrawing(ev) {
    this.startCoord = this.getCoordinates(ev);
    this.checkChangingColor();
    this.checkMovingState();
    if (this.curr.move !== true && this.curr.x !== 'undefined') {
      this.drawingState = true;
      this.img.src = this.canvas.canvas.toDataURL();
    }
    if (this.model.getState() === 'paintSame') {
      this.paintSameColor();
    }
    if (this.model.getState() === 'bucket') {
      this.bucket();
    }
    if (this.model.getState() === 'picker') {
      this.pickerTool();
    }
  }

  checkMovingState() {
    if ((this.startCoord.x > this.curr.x && this.startCoord.x < (this.curr.x + this.curr.width))
    && (this.startCoord.y > this.curr.y && this.startCoord.y
      < (this.curr.y + this.curr.height)) && this.model.getState() === 'shape') {
      this.curr.move = true;
    } else if ((((this.startCoord.x > (this.curr.x + this.curr.width)) || (this.startCoord.x < this.curr.x)) && this.model.getState() === 'shape')
  || (((this.startCoord.y
    > (this.curr.y + this.curr.height)) || this.startCoord.y < this.curr.y) && this.model.getState() === 'shape')) {
      this.Canvasview.redrawCanvas(this.img, this.model.getScale());
      this.curr = {};
    }
    if (this.curr.move === true) {
      this.curr.moveX = Math.abs(this.curr.x - this.startCoord.x);
      this.curr.moveY = Math.abs(this.curr.y - this.startCoord.y);
      this.drawingState = true;
    }
  }

  checkChangingColor() {
    if (this.left) {
      this.changeColor(0);
    } else {
      this.changeColor(1);
    }
  }

  getCoordinates(ev) {
    const rect = document.getElementById('canvas').getBoundingClientRect();
    return {
      x: (Math.round((ev.clientX - rect.left - (10 / 8)) / this.model.getPenSize())
      * this.model.getPenSize()),
      y: (Math.round((ev.clientY - rect.top - (10 / 8)) / this.model.getPenSize())
      * this.model.getPenSize()),

    };
  }

  penDrawing(x, y) {
    this.model.lineCoordinates(this.startCoord.x,
      this.startCoord.y, x, y);
    this.startCoord.x = x;
    this.startCoord.y = y;
  }

  lineDrawing(x, y) {
    this.Canvasview.redrawCanvas(this.img);
    this.model.lineCoordinates(this.startCoord.x,
      this.startCoord.y, x, y);
  }

  drawRectangle(x, y) {
    this.Canvasview.redrawCanvas(this.img);
    this.drawCarcass(x, y);
    this.canvas.context.stroke();
  }

  drawCarcass(x, y) {
    this.canvas.context.lineWidth = this.model.getPenSize();
    this.canvas.context.beginPath();
    this.canvas.context.moveTo(this.startCoord.x, this.startCoord.y);

    if (this.startCoord.x < x && this.startCoord.y > y) {
      this.canvas.context.lineTo(x, this.startCoord.y);
      this.canvas.context.lineTo(x, y);
      this.canvas.context.lineTo(this.startCoord.x, y);
      this.canvas.context.lineTo(this.startCoord.x, this.startCoord.y);
    } else if (this.startCoord.x > x && this.startCoord.y < y) {
      this.canvas.context.lineTo(x, this.startCoord.y);
      this.canvas.context.lineTo(x, y);
      this.canvas.context.lineTo(this.startCoord.x, y);
      this.canvas.context.lineTo(this.startCoord.x, this.startCoord.y);
    } else if (this.startCoord.x < x && this.startCoord.y < y) {
      this.canvas.context.lineTo(this.startCoord.x, y);
      this.canvas.context.lineTo(x, y);
      this.canvas.context.lineTo(x, this.startCoord.y);
      this.canvas.context.lineTo(this.startCoord.x, this.startCoord.y);
    } else if (this.startCoord.x > x && this.startCoord.y > y) {
      this.canvas.context.lineTo(this.startCoord.x, y);
      this.canvas.context.lineTo(x, y);
      this.canvas.context.lineTo(x, this.startCoord.y);
      this.canvas.context.lineTo(this.startCoord.x, this.startCoord.y);
    }
    this.canvas.context.closePath();
  }

  moveTool(x, y) {
    this.canvas.context.save();
    this.canvas.context.translate(x - this.startCoord.x,
      y - this.startCoord.y);
    this.Canvasview.redrawCanvas(this.img);
    this.canvas.context.restore();
  }

  eraserTool(x, y) {
    this.canvas.context.globalCompositeOperation = 'destination-out';
    this.canvas.context.fillStyle = '(rgba(0,0,0,1)';
    this.penDrawing(x, y);
    this.canvas.context.globalCompositeOperation = 'source-over';
  }

  pickerTool() {
    const color = this.canvas.context.getImageData(this.startCoord.x, this.startCoord.y, 1, 1).data;
    if (color[3] !== 0) {
      document.getElementById('rightBut').value = `#${Canvas.NumToHEX(color[0])}${Canvas.NumToHEX(color[1])}${Canvas.NumToHEX(color[2])}`;
    }
  }

  bucket() {
    const color = document.getElementById('rightBut').value;
    this.floodFill(this.startCoord.x, this.startCoord.y, color);
  }

  drawCircle(x, y) {
    this.Canvasview.redrawCanvas(this.img, this.model.getScale());
    this.model.drawCircle(this.startCoord.x,
      this.startCoord.y, x, y);
  }

  darkenLightenTool(x, y) {
    if (!this.shift) {
      this.model.lineCoordinates(this.startCoord.x,
        this.startCoord.y, x, y, 1);
    } else {
      this.model.lineCoordinates(this.startCoord.x,
        this.startCoord.y, x, y, -1);
    }
    this.startCoord.x = x;
    this.startCoord.y = y;
  }

  shapeTool(x, y) {
    this.Canvasview.redrawCanvas(this.img, this.model.getScale());
    if (typeof this.curr.move === 'undefined' || (this.curr.move === false && typeof this.curr.move !== 'undefined')) {
      this.canvas.context.fillStyle = '#00FFFF33';
      this.drawCarcass(x, y);
      this.canvas.context.fill();
      this.curr.x = Math.min(x, this.startCoord.x);
      this.curr.y = Math.min(y, this.startCoord.y);
      this.curr.width = Math.abs(this.startCoord.x - x);
      this.curr.height = Math.abs(this.startCoord.y - y);
      this.curr.move = false;
    }
    if (typeof this.curr.move !== 'undefined' && this.curr.move === true) {
      this.Canvasview.redrawCanvas(this.img, this.model.getScale());
      this.canvas.context.save();
      this.canvas.context.translate(x - this.curr.moveX, y - this.curr.moveY);
      this.canvas.context.fillRect(0, 0, this.curr.width, this.curr.height);
      this.canvas.context.restore();
      this.curr.x = x - this.curr.moveX;
      this.curr.y = y - this.curr.moveY;
    }
  }

  mirrorTool(x, y) {
    if (!this.shift && !this.ctrl) {
      this.model.lineCoordinates(this.startCoord.x,
        this.startCoord.y, x, y);
      this.model.lineCoordinates(this.canvas.width - this.startCoord.x,
        this.startCoord.y, this.canvas.width - x, y);
    } else if (this.shift && !this.ctrl) {
      this.model.lineCoordinates(this.startCoord.x,
        this.startCoord.y, x, y);
      this.model.lineCoordinates(this.startCoord.x,
        this.canvas.height - this.startCoord.y, x,
        this.canvas.height - y);
    } else if (!this.shift && this.ctrl) {
      this.model.lineCoordinates(this.startCoord.x,
        this.startCoord.y, x, y);
      this.model.lineCoordinates(this.canvas.width - this.startCoord.x,
        this.canvas.height - this.startCoord.y, this.canvas.width - x,
        this.canvas.height - y);
      this.model.lineCoordinates(this.canvas.width - this.startCoord.x,
        this.startCoord.y, this.canvas.width - x, y);
      this.model.lineCoordinates(this.startCoord.x,
        this.canvas.height - this.startCoord.y, x,
        this.canvas.height - y);
    }
    this.startCoord.x = x - this.curr.moveX;
    this.startCoord.y = y - this.curr.moveY;
  }

  draw(ev) {
    const currentCoords = this.getCoordinates(ev);
    if (this.drawingState) {
      if (this.startCoord.x === undefined) {
        this.startCoord = currentCoords;
      }
      if (this.model.getState() === 'pen') {
        this.penDrawing(currentCoords.x, currentCoords.y);
        return true;
      }
      if (this.model.getState() === 'line') {
        this.lineDrawing(currentCoords.x, currentCoords.y);
        return true;
      }
      if (this.model.getState() === 'rectangle') {
        this.drawRectangle(currentCoords.x, currentCoords.y);
        return true;
      }
      if (this.model.getState() === 'move') {
        this.moveTool(currentCoords.x, currentCoords.y);
        return true;
      }
      if (this.model.getState() === 'eraser') {
        this.eraserTool(currentCoords.x, currentCoords.y);
        return true;
      }
      if (this.model.getState() === 'circle') {
        this.drawCircle(currentCoords.x, currentCoords.y);
        return true;
      }
      if (this.model.getState() === 'lighten') {
        return this.darkenLightenTool(currentCoords.x, currentCoords.y);
      }
      if (this.model.getState() === 'shape') {
        this.shapeTool(currentCoords.x, currentCoords.y);
        return true;
      }
      if (this.model.getState() === 'mirror') {
        this.mirrorTool(currentCoords.x, currentCoords.y);
        return true;
      }
    }
    this.numCoord.innerHTML = `[${currentCoords.x} : ${currentCoords.y}]`;
    return false;
  }

  stopDrawing() {
    if (this.drawingState) {
      this.startCoord = {};
      this.drawingState = false;
      this.left = false;
    }
  }

  mouseOut() {
    this.numCoord.style.visibility = 'hidden';
  }

  mouseEnter() {
    this.numCoord.style.visibility = 'visible';
  }

  deleteSelection() {
    if (this.model.getState() === 'shape') {
      this.canvas.context.clearRect(this.curr.x, this.curr.y, this.curr.width, this.curr.height);
      this.img.src = this.canvas.canvas.toDataURL();
      this.img.onload = () => {
        this.canvas.context.fillStyle = '#00FFFF33';
        this.canvas.context.fillRect(this.curr.x, this.curr.y, this.curr.width, this.curr.height);
      };
    }
  }

  static getPixel(pixelData, x, y) {
    if (x < 0 || y < 0 || x >= pixelData.width || y >= pixelData.height) {
      return [-1, -1, -1, -1];
    }
    const pixel = (y * pixelData.width + x) * 4;
    return pixelData.data.slice(pixel, pixel + 4);
  }

  floodFill(x, y, fillColor) {
    const imageData = this.canvas.context.getImageData(0, 0, this.canvas.width, this.canvas.height);
    const pixelData = {
      width: imageData.width,
      height: imageData.height,
      data: imageData.data,
    };

    const fill = Canvas.HextoRGB(fillColor);

    const targetColor = Canvas.getPixel(pixelData, x, y);
    if (!Canvas.sameColors(fill, targetColor)) {
      const pixelsToCheck = [x, y];
      while (pixelsToCheck.length > 0) {
        const y1 = pixelsToCheck.pop();
        const x1 = pixelsToCheck.pop();
        if (Canvas.sameColors(Canvas.getPixel(pixelData, x1, y1), targetColor)) {
          Canvas.PaintPiskel(pixelData, x1, y1, fill);
          pixelsToCheck.push(x1 + 1, y1);
          pixelsToCheck.push(x1 - 1, y1);
          pixelsToCheck.push(x1, y1 + 1);
          pixelsToCheck.push(x1, y1 - 1);
        }
      }
      this.canvas.context.putImageData(imageData, 0, 0);
    }
  }

  static PaintPiskel(pixelData, x, y, color) {
    const pixel = (y * pixelData.width + x) * 4;
    const arr = pixelData;
    // eslint-disable-next-line prefer-destructuring
    arr.data[pixel] = color[0];
    // eslint-disable-next-line prefer-destructuring
    arr.data[pixel + 1] = color[1];
    // eslint-disable-next-line prefer-destructuring
    arr.data[pixel + 2] = color[2];
    // eslint-disable-next-line prefer-destructuring
    arr.data[pixel + 3] = 255;
  }

  static NumToHEX(num) {
    const hex = num.toString(16);
    return hex.length === 1 ? `0${hex}` : hex;
  }

  static HextoRGB(color) {
    const arr = [];
    if (color.slice(1, 3) === 0) {
      arr.push(0);
    } else {
      arr.push(parseInt(color.slice(1, 3), 16));
    }
    if (+color.slice(3, 5) === 0) {
      arr.push(0);
    } else {
      arr.push(parseInt(color.slice(3, 5), 16));
    }
    if (+color.slice(5, 7) === 0) {
      arr.push(0);
    } else {
      arr.push(parseInt(color.slice(5, 7), 16));
    }
    return arr;
  }

  static sameColors(color1, color2, i = 0) {
    return color1[0] === color2[i] && color1[1] === color2[i + 1]
     && color1[2] === color2[i + 2];
  }

  paintSameColor() {
    const imageData = this.canvas.context.getImageData(0, 0, this.canvas.width, this.canvas.height);
    const color = document.getElementById('rightBut').value;
    const arr = Canvas.HextoRGB(color);
    const pixelData = {
      width: imageData.width,
      height: imageData.height,
      data: imageData.data,
    };
    const color2 = this.canvas.context.getImageData(this.startCoord.x, this.startCoord.y, 1, 1);
    const { data } = color2;
    for (let i = 0; i < pixelData.data.length; i += 4) {
      if (Canvas.sameColors(data, pixelData.data, i)) {
        // eslint-disable-next-line prefer-destructuring
        pixelData.data[i] = arr[0];
        // eslint-disable-next-line prefer-destructuring
        pixelData.data[i + 1] = arr[1];
        // eslint-disable-next-line prefer-destructuring
        pixelData.data[i + 2] = arr[2];
      }
    }
    this.canvas.context.putImageData(imageData, 0, 0);
  }

  changeColor(flag) {
    if (flag === 1) {
      this.canvas.context.strokeStyle = `${document.getElementById('rightBut').value}`;
      this.canvas.context.fillStyle = `${document.getElementById('rightBut').value}`;
    } else {
      this.canvas.context.strokeStyle = `${document.getElementById('leftBut').value}`;
      this.canvas.context.fillStyle = `${document.getElementById('leftBut').value}`;
    }
  }

  static replaceColor() {
    const color1 = document.getElementById('rightBut').value;
    const color2 = document.getElementById('leftBut').value;
    document.getElementById('leftBut').value = color1;
    document.getElementById('rightBut').value = color2;
    this.canvas.context.fillStyle = color2;
  }

  drawing() {
    document.getElementById('canvas').addEventListener('mousedown', (ev) => { this.startDrawing(ev); }, false);
    document.getElementById('canvas').addEventListener('mousemove', ev => this.draw(ev), false);
    document.getElementById('canvas').addEventListener('mouseup', ev => this.stopDrawing(ev), false);
    document.getElementById('canvas').addEventListener('mouseleave', ev => this.mouseOut(ev), false);
    document.getElementById('canvas').addEventListener('mouseenter', ev => this.mouseEnter(ev), false);
  }

  redrawCanvas(frame, x = 0, y = 0) {
    return this.Canvasview.redrawCanvas(frame, x, y);
  }

  clearCanvas() {
    return this.Canvasview.clearCanvas();
  }
}
