export default class ToolModel {
  constructor(st) {
    let state = st;
    let scale = 1;
    this.setScale = (scalePen) => {
      scale = scalePen;
    };
    this.getScale = () => scale;

    let CanvasSize = 32;
    this.setCanvasSize = (canvasSize) => {
      CanvasSize = canvasSize;
    };
    this.getCanvasSize = () => CanvasSize;

    let penSize = 32;
    this.setPenSize = (size) => {
      penSize = size;
    };
    this.getPenSize = () => penSize;

    this.setState = (sta) => {
      state = sta;
    };
    this.getState = () => state;
  }

  lineCoordinates(x1, y1, x2, y2, flag = 0) {
    let x; let y; let xe; let ye; let i;
    const dx = x2 - x1;
    const dy = y2 - y1;
    const dx1 = Math.abs(dx);
    const dy1 = Math.abs(dy);
    let px = 2 * dy1 - dx1;
    let py = 2 * dx1 - dy1;

    if (dy1 <= dx1) {
      if (dx >= 0) {
        x = x1; y = y1; xe = x2;
      } else {
        x = x2; y = y2; xe = x1;
      }
      this.drawPixel(x, y, flag);
      for (i = 0; x < xe; i += 1) {
        x += this.getPenSize();
        if (px < 0) {
          px += 2 * dy1;
        } else {
          y = this.nextPixel(dx, dy, y);
          px += 2 * (dy1 - dx1);
        }
        this.drawPixel(x, y, flag);
      }
    } else {
      if (dy >= 0) {
        x = x1; y = y1; ye = y2;
      } else {
        x = x2; y = y2; ye = y1;
      }
      this.drawPixel(x, y, flag);
      for (i = 0; y < ye; i += 1) {
        y += this.getPenSize();
        if (py <= 0) {
          py += 2 * dx1;
        } else {
          x = this.nextPixel(dx, dy, x);
          py += 2 * (dx1 - dy1);
        }
        this.drawPixel(x, y, flag);
      }
    }
  }

  nextPixel(dx, dy, coordinates) {
    let coord = coordinates;
    if ((dx < 0 && dy < 0) || (dx > 0 && dy > 0)) {
      coord += this.getPenSize();
    } else {
      coord -= this.getPenSize();
    }
    return coord;
  }

  drawPixel(x, y, flag) {
    if (flag) {
      const p = `${ToolModel.lighten(document.getElementById('canvas').getContext('2d').getImageData(x, y, 1, 1).data, 0.1 * flag)}`;
      if (p !== '1111') {
        document.getElementById('canvas').getContext('2d').fillStyle = p;
      } else {
        document.getElementById('canvas').getContext('2d').fillStyle = 'transparent';
      }
    }
    document.getElementById('canvas').getContext('2d').fillRect(x, y, this.getPenSize(), this.getPenSize());
  }

  drawCircle(xm, ym, x1, y1) {
    const r = (Math.sqrt(((ym - y1) ** 2) + ((xm - x1) ** 2))) / 2;
    const xc = (xm + x1) / 2;
    const yc = (ym + y1) / 2;
    let x = -r;
    let y = 0;
    let err = 1 - r;
    do {
      this.setPixel(xc, yc, x, y);
      if (err + y > 0) {
        x += this.getPenSize();
        err += x * this.getPenSize() * 2 + this.getPenSize();
      }
      if (err + x <= 0) {
        y += this.getPenSize();
        err += y * this.getPenSize() * 2 + this.getPenSize();
      }
    } while (x < 0);
    document.getElementById('canvas').getContext('2d').fillRect(xc, yc - y,
      this.getPenSize(), this.getPenSize());
    document.getElementById('canvas').getContext('2d').fillRect(xc, yc + y,
      this.getPenSize(), this.getPenSize());
  }

  setPixel(x0, y0, x1, y1) {
    const xc = x0;
    const yc = y0;
    const x = x1;
    const y = y1;
    document.getElementById('canvas').getContext('2d').fillRect(xc - x,
      yc + y, this.getPenSize(), this.getPenSize());
    document.getElementById('canvas').getContext('2d').fillRect(xc - x,
      yc - y, this.getPenSize(), this.getPenSize());
    document.getElementById('canvas').getContext('2d').fillRect(xc + x, yc - y,
      this.getPenSize(), this.getPenSize());
    document.getElementById('canvas').getContext('2d').fillRect(xc + x, yc + y,
      this.getPenSize(), this.getPenSize());
  }

  static lighten(col, light) {
    if (col[3] === 0) {
      return '1111';
    }
    let r = col[0];
    let g = col[1];
    let b = col[2];

    if (light > 0) {
      r = (1 - light) * r + light * 255;
      g = (1 - light) * g + light * 255;
      b = (1 - light) * b + light * 255;
    } else {
      r *= (1 + light / 4);
      g *= (1 + light / 4);
      b *= (1 + light / 4);
    }

    return `rgb(${r}, ${g}, ${b})`;
  }
}
