export default class FrameView {
  constructor(frameList) {
    this.FrameList = frameList;
  }

  static clearFrame(frame) {
    frame.getContext('2d').clearRect(0, 0, frame.width, frame.height);
  }

  static redrawFrame(frame, img) {
    FrameView.clearFrame(frame);
    frame.getContext('2d').drawImage(img, 0, 0, img.width, img.height, 0, 0, frame.width, frame.height);
  }

  CreateFrame(number) {
    const arr = [];
    const li = document.createElement('li');
    arr.push(li);

    const div = document.createElement('div');
    div.id = 'num';
    div.innerText = `${number}`;
    li.appendChild(div);

    const canvas = document.createElement('canvas');
    canvas.id = 'frame';
    canvas.dataset.number = `${number}`;
    li.appendChild(canvas);
    canvas.setAttribute('draggable', true);

    arr.push(canvas);

    arr.push(FrameView.createButton('delete', '../image/remove.png'));
    arr.push(FrameView.createButton('copy', '../image/copy.png'));
    arr.push(FrameView.createButton('move', '../image/move.png'));

    for (let i = 2; i < arr.length; i += 1) {
      li.appendChild(arr[i]);
    }

    this.addToList(li);
    return arr;
  }

  static createButton(id, ImageSrc) {
    const button = document.createElement('button');
    button.id = id;
    button.innerHTML = `<img src=${ImageSrc}>`;
    return button;
  }

  addToList(elem) {
    this.FrameList.appendChild(elem);
  }
}
