import ToolModel from '../model/ToolModel';
import Canvas from './Canvas';
import FrameController from './FrameController';
import AnimationFrameController from './AnimationFrameController';

export default class ToolController {
  constructor(canv) {
    this.model = new ToolModel();
    this.canvas = new Canvas(this.model, canv);
    this.frameController = new FrameController(canv, this.canvas);
    this.Animation = new AnimationFrameController();
    this.drawingState = false;
    this.CanvasField = canv;
    this.scaleCanv = 0;
    this.ImgWidth = 32;
    this.sizeNum = document.getElementById('numSize');
    this.keyChange = false;
    this.currentKey = null;
  }

  active() {
    const buttons = document.querySelectorAll('.first ul li');
    for (let i = 0; i < buttons.length; i += 1) {
      if (this.model.getState() === buttons[i].id) {
        buttons[i].style.borderColor = 'gold';
      } else {
        buttons[i].style.borderColor = 'transparent';
      }
    }
  }

  addToolsListeners() {
    this.setButtonListeners();
    this.canvas.drawing();
    this.frameController.addFrameListeners();
    this.Animation.addEventListeners();
    this.setColorListeners();
    this.setKeyListeners();
    this.addKeyShortcuts();
  }

  addKeyShortcuts() {
    const keys = document.querySelectorAll('#toolShorts li');
    for (let i = 0; i < keys.length; i += 1) {
      keys[i].addEventListener('click', () => {
        keys[i].style.borderColor = 'red';
        this.keyChange = true;
        this.currentKey = keys[i];
      });
    }
  }

  setButtonListeners() {
    const obj = document.querySelectorAll('.first ul li');
    this.resizeCanvas();
    this.setPenSize();
    for (let i = 0; i < obj.length; i += 1) {
      obj[i].addEventListener('click', () => {
        this.model.setState(obj[i].id);
        this.active();
      });
    }
    document.getElementById('cross').addEventListener('click', () => {
      document.getElementById('shortcuts').style.visibility = 'hidden';
    });
    document.getElementById('keyboard').addEventListener('click', () => {
      document.getElementById('shortcuts').style.visibility = 'visible';
    });
  }

  setColorListeners() {
    document.getElementById('rightBut').addEventListener('change', () => {
      this.canvas.changeColor(1);
    });
    document.getElementById('leftBut').addEventListener('change', () => {
      this.canvas.changeColor(0);
    });
    document.oncontextmenu = (event) => {
      this.canvas.left = true;
      const event1 = event || window.event;
      // eslint-disable-next-line no-unused-expressions
      event1.preventDefault ? event1.preventDefault() : event1.returnValue = false;
    };
    document.getElementById('changeColor').addEventListener('click', Canvas.replaceColor);
  }

  setKeyListeners() {
    document.addEventListener('keydown', (event) => {
      if (!this.keyChange) {
        if (event.shiftKey) {
          this.canvas.shift = true;
        } else if (event.ctrlKey) {
          this.canvas.ctrl = true;
        } else if (event.key === 'Delete') {
          event.preventDefault();
          this.canvas.deleteSelection();
        } else if (!this.keyChange) {
          const keys = document.querySelectorAll('#toolShorts li');
          for (let i = 0; i < keys.length; i += 1) {
            if (event.key.toUpperCase() === keys[i].innerHTML) {
              document.getElementById(keys[i].dataset.tool).click();
            }
          }
        }
      } else if (event.keyCode > 20 && this.keyChange) {
        const keys = document.querySelectorAll('#toolShorts li');
        for (let i = 0; i < keys.length; i += 1) {
          if (event.key.toUpperCase() !== keys[i].innerHTML && keys[i] === this.currentKey) {
            keys[i].innerHTML = event.key.toUpperCase();
          } else if (event.key.toUpperCase() === keys[i].innerHTML && keys[i] !== this.currentKey) {
            keys[i].innerHTML = '?';
          }
        }
      }
    }, false);
    document.addEventListener('keyup', () => {
      this.canvas.shift = false;
      this.canvas.ctrl = false;
      this.keyChange = false;
      this.currentKey.style.borderColor = 'gold';
    }, false);
  }

  ActivePen() {
    const buttons = document.querySelectorAll('#penSizes ul li');
    for (let i = 0; i < buttons.length; i += 1) {
      if (this.model.getScale() === +buttons[i].dataset.pen) {
        buttons[i].style.borderColor = 'gold';
      } else {
        buttons[i].style.borderColor = 'transparent';
      }
    }
  }

  static ActiveSize(elem) {
    const sizes = document.querySelectorAll('#TransformTool li');
    for (let i = 0; i < sizes.length; i += 1) {
      sizes[i].style.borderColor = 'transparent';
    }
    const size = elem;
    size.style.borderColor = 'gold';
  }

  updatePenSize() {
    this.model.setPenSize(this.model.getScale()
    * (this.CanvasField.width / this.model.getCanvasSize()));
  }

  setPenSize() {
    const pens = document.querySelectorAll('#penSizes ul li');
    for (let i = 0; i < pens.length; i += 1) {
      pens[i].addEventListener('click', () => {
        this.model.setScale(+pens[i].dataset.pen);
        this.updatePenSize();
        this.ActivePen();
        localStorage.setItem('pen-size', +pens[i].dataset.pen);
      });
    }
  }

  resizeCanvas() {
    const img = new Image();
    const sizes = document.querySelectorAll('#TransformTool li');
    for (let i = 0; i < sizes.length; i += 1) {
      // eslint-disable-next-line no-loop-func
      sizes[i].addEventListener('click', () => {
        img.src = this.CanvasField.canvas.toDataURL();
        this.ImgOnload(img, sizes[i]);
        this.CanvasField.context.resetTransform();
        this.CanvasField.context.restore();
      }, false);
    }
  }

  ImgOnload(img1, elem) {
    const img = img1;
    let scale;
    img.onload = () => {
      scale = this.ImgWidth / +elem.dataset.size;
      this.drawImageScaled(img, scale);
      this.model.setCanvasSize(+elem.dataset.size);
      localStorage.setItem('canvas-size', +elem.dataset.size);
      this.sizeNum.innerHTML = `[${+elem.dataset.size} x ${+elem.dataset.size}]`;
      this.updatePenSize();
      ToolController.ActiveSize(elem);
      this.ImgWidth = +elem.dataset.size;
    };
  }

  drawImageScaled(img, scale) {
    this.CanvasField.context.save();
    this.CanvasField.context.clearRect(0, 0, 832, 832);
    this.CanvasField.context.imageSmoothingEnabled = false;
    this.CanvasField.context.translate(-(scale - 1) * 832 / 2,
      -(scale - 1) * 832 / 2);
    this.CanvasField.context.scale(scale, scale);
    this.CanvasField.context.drawImage(img, 0, 0, 832, 832);
    this.CanvasField.context.restore();
  }
}
