import ToolController from './controller/ToolController';
import Export from './controller/ExportController';


const start = () => {
  const canvas = {
    canvas: document.getElementById('canvas'),
    context: document.getElementById('canvas').getContext('2d'),
    width: document.getElementById('canvas').width,
    height: document.getElementById('canvas').height,
  };
  const mod = new ToolController(canvas);
  mod.addToolsListeners();
  const exprt = new Export(mod.model);
  exprt.addEventListeners();
  if (localStorage.length > 0) {
    for (let i = 0; i < localStorage.length; i += 1) {
      if (localStorage.key(i) === 'pen-size') {
        mod.model.setScale(localStorage.getItem(localStorage.key(i)));
        document.querySelector(`*[data-pen="${localStorage.getItem(localStorage.key(i))}"]`).click();
      } else if (localStorage.key(i) === 'canvas-size') {
        mod.model.setCanvasSize(localStorage.getItem(localStorage.key(i)));
        document.querySelector(`*[data-size="${localStorage.getItem(localStorage.key(i))}"]`).click();
      }
    }
  }
};

function insertFile(fileData, filename) {
  window.gapi.load('client', () => {
    window.gapi.client.load('drive', 'v3', () => {
      const boundary = String(Math.random()).slice(2);
      const delimiter = `\r\n--${boundary}\r\n`;
      const closedelim = `\r\n--${boundary}--`;

      const reader = new FileReader();
      reader.readAsArrayBuffer(fileData);
      reader.onload = () => {
        const contentType = fileData.type || 'application/octet-stream';
        const metadata = {
          title: filename,
          mimeType: fileData.type,
        };

        const base64Data = btoa(reader.result);
        const multipartRequestBody = `${delimiter
        }Content-Type: application/json\r\n\r\n${
          JSON.stringify(metadata)
        }${delimiter
        }Content-Type: ${contentType}\r\n`
                + 'Content-Transfer-Encoding: base64\r\n'
                + `\r\n${
                  base64Data
                }${closedelim}`;

        const request = window.gapi.client.request({
          path: '/upload/drive/v3/files',
          method: 'POST',
          params: { uploadType: 'multipart' },
          headers: {
            'Content-Type': `multipart/mixed; boundary="${boundary}"`,
          },
          body: multipartRequestBody,
        });
        request.then(() => {
          document.querySelector('.upload').style.borderColor = 'green';
        }, () => {
          document.querySelector('.upload').style.borderColor = 'red';
        });
      };
    });
  });
}

function upload() {
  if (document.getElementById('logIn').style.display === 'none') {
    const file = document.getElementById('uploading').files[0];
    insertFile(file, 'Piscel');
  } else {
    document.getElementById('logIn').click();
  }
}
function logOK(user) {
  document.getElementById('logIn').style.display = 'none';
  document.getElementById('logOut').style.display = 'block';
  document.getElementById('userPhoto').src = user.getBasicProfile().getImageUrl();
  document.getElementById('userName').innerText = user.getBasicProfile().getName().toString();
  document.querySelector('.NamePhoto').style.opacity = '1';
}

function logError() {
  document.getElementById('logIn').innerText = 'error';
}

function LogIn() {
  if (window.gapi && window.gapi.auth2) {
    window.gapi.auth2.getAuthInstance().signIn({
    }).then(logOK, logError);
  }
}

function logOutOK() {
  document.getElementById('logIn').style.display = 'block';
  document.getElementById('logOut').style.display = 'none';
  document.querySelector('.NamePhoto').style.opacity = '0';
}

function LogOut() {
  window.gapi.auth2.getAuthInstance().signOut().then(logOutOK, logError);
}

window.onload = () => {
  global.console.log('onload');
  window.gapi.load('auth2', () => {
    window.gapi.auth2.init({
      client_id: '261560799158-uhi1fc0jnl6ept2ocj7u96boukps0hcn.apps.googleusercontent.com',
      apiKey: 'AIzaSyDZ1lvuvjaChMCUlEgxJqzGJnJ4hTLu19U',
      discoveryDocs: ['https://www.googleapis.com/discovery/v1/apis/drive/v3/rest'],
      scope: 'https://www.googleapis.com/auth/drive.file',
    }).then(() => {
      document.getElementById('logIn').addEventListener('click', LogIn);
      document.getElementById('logOut').addEventListener('click', LogOut);
      document.getElementById('uploading').addEventListener('input', upload);
      start();
    }, error => global.console.log(`error initialization ${error}`));
  });
};
