import gifshot from '../../gifshot/src/gifshot';
import APNGencoder from '../canvas2apng';

export default class Export {
  constructor(mod) {
    this.model = mod;
  }

  addEventListeners() {
    document.getElementById('gif').addEventListener('click', Export.asGIF);
    document.getElementById('apng').addEventListener('click', Export.asAPNG);
    document.getElementById('pscl').addEventListener('click', () => this.asPSCL());
    document.getElementById('importing').addEventListener('input', Export.import);
  }


  asPSCL() {
    const obj = {};
    obj.pen = this.model.getScale();
    obj.size = this.model.getCanvasSize();
    obj.fps = document.querySelector('#slider input').value;
    obj.pictures = [];
    const frames = document.querySelectorAll('#frame');
    for (let i = 0; i < frames.length; i += 1) {
      obj.pictures.push(frames[i].toDataURL('image/png'));
    }
    const json = JSON.stringify(obj);
    const a = document.createElement('a');
    const file = new Blob([json], { type: 'text/plain' });
    a.href = URL.createObjectURL(file);
    a.download = 'newPiscel.pscl';
    a.click();
  }

  static import() {
    const file = document.getElementById('importing').files[0];
    const reader = new FileReader();
    reader.readAsText(file);
    reader.onload = (e) => {
      const content = JSON.parse(e.target.result);
      document.querySelector('#slider input').value = content.fps;
      document.querySelector(`*[data-pen = "${content.pen}"`).click();
      document.querySelector(`*[data-size = "${content.size}"`).click();
      const button = document.getElementById('add');
      let frames = document.querySelectorAll('#frame');
      for (let i = frames.length; i < content.pictures.length; i += 1) {
        button.click();
      }
      frames = document.querySelectorAll('#frame');
      for (let i = 1; i < content.pictures.length; i += 1) {
        const img = new Image();
        img.src = content.pictures[i];
        img.onload = () => {
          frames[i].getContext('2d').drawImage(img, 0, 0, img.width, img.height, 0, 0, frames[i].width, frames[i].height);
        };
      }
      frames[0].click();
    };
  }

  static asGIF() {
    const frames = document.querySelectorAll('#frame');
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');
    canvas.width = 600;
    canvas.height = 600;
    const Frimages = [];
    ctx.fillStyle = '#fff';
    for (let i = 0; i < frames.length; i += 1) {
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      ctx.drawImage(frames[i], 0, 0, frames[i].width, frames[i].height, 0, 0, 400, 400);
      const img = new Image();
      img.src = canvas.toDataURL('image/png');
      Frimages.push(img);
    }
    gifshot.createGIF({
      images: Frimages,
      interval: 1 / document.querySelector('#slider input').value,
      gifWidth: 128,
      gifHeight: 128,
    }, (obj) => {
      if (!obj.error) {
        const imageTemp = obj.image;
        const animatedImage = document.createElement('img');
        animatedImage.src = imageTemp;
        const linkToSave = document.createElement('a');
        linkToSave.setAttribute('download', 'piskelGIF.gif');
        linkToSave.setAttribute('href', animatedImage.src);
        linkToSave.click();
      }
    });
  }

  static asAPNG() {
    const frames = document.querySelectorAll('#frame');
    const canvas = document.createElement('canvas');
    const ctx = canvas.getContext('2d');
    const encoder = new APNGencoder(canvas);
    encoder.setRepeat(0);
    encoder.setDelay(100 / document.querySelector('#slider input').value);
    encoder.setDispose(0);
    encoder.setBlend(1);
    encoder.start();
    for (let i = 0; i < frames.length; i += 1) {
      ctx.fillStyle = '#000';
      ctx.fillRect(0, 0, canvas.width, canvas.height);
      ctx.drawImage(frames[i], 0, 0, 832, 832, 0, 0, 832, 832);
      encoder.addFrame(ctx);
      ctx.clearRect(0, 0, canvas.width, canvas.height);
      encoder.addFrame(ctx);
    }
    encoder.finish();

    const a = window.document.createElement('a');
    const out = encoder.stream();
    a.href = URL.createObjectURL(new Blob([new Uint8Array(out.bin)], { type: 'image/png' }));
    a.download = 'file.apng';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
  }
}
