export default class AnimationView {
  constructor() {
    this.animationFrame = {
      frame: document.getElementById('animation'),
      context: document.getElementById('animation').getContext('2d'),
      width: document.getElementById('animation').clientWidth,
      height: document.getElementById('animation').height,
    };
  }

  clearAnimationFrame() {
    this.animationFrame.context.clearRect(0, 0, this.animationFrame.width,
      this.animationFrame.height);
  }

  drawAnimation(frame) {
    this.clearAnimationFrame();
    this.animationFrame.context.drawImage(frame, 0, 0, frame.width, frame.height, 0, 0,
      200, 200);
  }
}
