import FrameModel from '../model/FrameModel';
import FrameView from '../view/FrameView';

export default class FrameController {
  constructor(canvas, canvasController) {
    this.canvas = canvas;
    this.frameList = document.getElementById('frames');
    this.FrameView = new FrameView(this.frameList);
    this.CanvasController = canvasController;
    this.frames = document.querySelectorAll('#frame');
    this.FrameModel = new FrameModel(this.frames[0]);
    this.FramesCount = 1;
    this.draggable = null;
  }

  addFrameListeners() {
    this.canvas.canvas.addEventListener('mouseup', () => { this.beginRender(); });
    this.addButtonListeners();
    const child = document.querySelectorAll('#frames li');
    child[0].querySelector('#copy').addEventListener('click', () => { this.copyFrame(child[0], child[0].querySelector('canvas')); });
    child[0].querySelector('#delete').addEventListener('click', () => { this.removeFrame(child[0], child[0].querySelector('canvas')); });
    this.activeFrame();
    this.addClickFrame();
    FrameController.HiddenButton();
  }

  setDraggable() {
    const canvases = document.querySelectorAll('#frame');
    for (let i = 0; i < canvases.length; i += 1) {
      canvases[i].addEventListener('dragstart', event => this.dragStart(event), false);
      canvases[i].addEventListener('dragEnd', event => FrameController.dragEnd(event), false);
      canvases[i].addEventListener('dragover', FrameController.dragOver);
      canvases[i].addEventListener('dragenter', FrameController.dragEnter);
      canvases[i].addEventListener('dragleave', event => FrameController.dragLeave(event));
      canvases[i].addEventListener('drop', event => this.dragDrop(event), false);
    }
  }

  dragStart(event) {
    this.draggable = event.target;
  }

  static dragEnter(e) {
    const ev = e;
    ev.preventDefault();
    ev.target.style.opacity = '.5';
    ev.target.style.borderStyle = 'dashed';
    ev.target.style.borderColor = 'gold';
  }

  static dragLeave(event) {
    const e = event;
    e.target.style.borderStyle = 'dashed';
    e.target.style.borderColor = 'gold';
  }

  static dragOver(event) {
    event.preventDefault();
  }

  dragDrop(event) {
    const e = event;
    e.preventDefault();
    this.draggable.style.borderStyle = 'solid';
    this.draggable.style.opacity = '1';
    e.target.style.borderStyle = 'solid';
    e.target.style.opacity = '1';
    const img = FrameController.CreateImage(this.draggable.toDataURL());
    FrameView.redrawFrame(this.draggable, event.target);
    img.onload = () => {
      FrameView.redrawFrame(event.target, img);
      this.CanvasController.redrawCanvas(img);
      this.FrameModel.setCurrentFrame(event.target);
      this.activeFrame();
    };
  }

  addClickFrame() {
    const arr = document.querySelectorAll('#frame');
    for (let i = 0; i < arr.length; i += 1) {
      arr[i].addEventListener('click', () => {
        this.FrameModel.setCurrentFrame(arr[i]);
        this.activeFrame();
        const img = FrameController.CreateImage(arr[i].toDataURL());
        img.onload = () => {
          this.CanvasController.redrawCanvas(img);
        };
      });
    }
  }

  static CreateImage(url) {
    const img = new Image();
    img.src = url;
    return img;
  }

  activeFrame() {
    const arr = document.querySelectorAll('#frame');
    for (let i = 0; i < arr.length; i += 1) {
      arr[i].style.borderColor = 'transparent';
    }
    this.FrameModel.getCurrentFrame().style.borderColor = 'gold';
  }

  addButtonListeners() {
    document.getElementById('add').addEventListener('click', () => { this.addFrame(); });
  }

  addFrame() {
    this.CanvasController.clearCanvas();
    this.FramesCount += 1;
    const arr = this.FrameView.CreateFrame(this.FramesCount);

    arr[2].addEventListener('click', () => this.removeFrame(arr[0], arr[1]));
    arr[3].addEventListener('click', () => this.copyFrame(arr[0], arr[1]));
    this.FrameModel.setCurrentFrame(arr[1]);
    FrameController.HiddenButton();
    this.activeFrame();
    this.addClickFrame();
    this.setDraggable();
  }

  removeFrame(li, canvas) {
    const removeCanvas = canvas;
    this.frameList.removeChild(li);

    this.FramesCount -= 1;

    if (+removeCanvas.dataset.number === 1) {
      this.FrameModel.setCurrentFrame(FrameController.getDatasetFrame(+canvas.dataset.number + 1));
    } else {
      this.FrameModel.setCurrentFrame(FrameController.getDatasetFrame(+canvas.dataset.number - 1));
    }
    this.activeFrame();
    this.setFramesNumber(+canvas.dataset.number - 1);
    this.CanvasController.redrawCanvas(this.FrameModel.getCurrentFrame());
    this.addClickFrame();
    FrameController.HiddenButton();
  }

  copyFrame(li, canvas) {
    this.FramesCount += 1;
    const arr = this.FrameView.CreateFrame(+canvas.dataset.number + 1);
    li.after(arr[0]);
    this.FrameModel.setCurrentFrame(arr[1]);
    FrameView.redrawFrame(arr[1], canvas);
    this.setFramesNumber(+canvas.dataset.number + 1);
    arr[2].addEventListener('click', () => this.removeFrame(arr[0], arr[1]));
    arr[3].addEventListener('click', () => this.copyFrame(arr[0], arr[1]));
    this.activeFrame();
    this.addClickFrame();
    FrameController.HiddenButton();
  }

  setFramesNumber(num) {
    const frames = document.querySelector('#frames').children;
    for (let i = (num); i < this.FramesCount; i += 1) {
      frames[i].querySelector('canvas').dataset.number = i + 1;
      frames[i].querySelector('#num').innerHTML = i + 1;
    }
  }

  static HiddenButton() {
    const buttons = document.querySelectorAll('#delete');
    buttons[0].style.visibility = 'visible';
    if (buttons.length === 1) {
      buttons[0].style.visibility = 'hidden';
    } else {
      for (let i = 0; i < buttons[i]; i += 1) {
        buttons[i].style.visibility = 'visible';
      }
    }
  }

  beginRender() {
    return FrameView.redrawFrame(this.FrameModel.getCurrentFrame(), this.canvas.canvas);
  }

  static getDatasetFrame(number) {
    return document.querySelector(`*[data-number="${number}"]`);
  }
}
